.precip {
  position: absolute;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  overflow: hidden;
}


.pre {
  z-index: 9;
}

.pre_back {
  z-index: 4;
}


.rain {
  position: absolute;
  width: 0;
  height: 0;
  background-color: #cdf;
  top: 0;
  left: 0;
  opacity: 0.9;
}

.rain:nth-of-type(2) {
  background-color: #8cf;
  top: 2vh;
  opacity: 0.6;

}

.drop {
  position: absolute;
  width: calc((1vmax + 1vmin)/2);
  height: calc((1vmax + 1vmin)/2);
  background-color: inherit;
  animation: fall 0.7s normal infinite;
  z-index: inherit;
}

.hail {
  border-radius: 50%;
  background-color: #8cf;
  opacity: 0.5;
}

.rain {
  opacity: 0.7;
  background-color: #cdf;
  width: calc((1vmax + 1vmin)/6);
  height: calc((1vmax + 1vmin)*2);
}

.snow {
  background-color: transparent;
}

.snow_shade {
  width: 0;
  height: 0;
  position: absolute;
  left: 0;
  top: 0;
  border-left: calc((1vmax + 1vmin)/3) solid transparent;
  border-right: calc((1vmax + 1vmin)/3) solid transparent;
  border-top: calc((1vmax + 1vmin)/2) solid #aee;
  background-color: transparent;
  animation: spin 20s ease-in-out infinite;
}

.snow_light {
  position: absolute;
  left: calc((-0.3vmax - 0.3vmin));
  top: calc(-0.7*(1vmax + 1vmin));
  border-left: calc((1vmax + 1vmin)/3) solid transparent;
  border-right: calc((1vmax + 1vmin)/3) solid transparent;
  border-bottom: calc((1vmax + 1vmin)/2) solid #cff;
}

.fog {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-image: linear-gradient(transparent, #ccc);
  opacity: 0.8;
}

@keyframes fall {
  from {
      opacity: 0;
      margin-left: 0;
      margin-top: 0;
    }
  
    to {
      opacity: 1;
      margin-left: 1vw;
      margin-top: 30vh;
    }
}