.hill {
  height: fit-content;
  position: absolute;
  width: fit-content;
  bottom: 0;
  left: 10vmax;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 7;
}

.hill:nth-of-type(1) {
  transform: scale(0.7);
}

.hill:nth-of-type(2) {
  bottom: 4vmax;
  left: 40vmax;
  transform: scale(0.4);
}

.hill:nth-of-type(3) {
  bottom: 2vmax;
  left: 70vmax;
  transform: scale(0.5);
}

.hill__hill {
  background-image: linear-gradient(#bfb, #afa, #2c2, #3a7);
  width: 30vmax;
  height: 10vmax;
  border-radius: 14vmax 14vmax 3vmax 3vmax;
}

@media (min-aspect-ratio: 2) {
  .hill:nth-of-type(1) {
    bottom: calc( - (4vw - 8vh)*8);
  }

  .hill:nth-of-type(2) {
    bottom: calc(4vw - (4vw - 8vh)*4);
  }

  .hill:nth-of-type(3) {
    bottom: calc(2vw - (2vw - 4vh)*6);
  }
}