.wind {
  animation: floating 10s ease-in-out infinite;
  max-width: 100%;
  height: fit-content;
  box-sizing: content-box;
  position: relative;
  top: 0;
  z-index: 5;
}

.wind :nth-child(2) {
  left: 40vw;
}

.wind_clear {
  opacity: 0.3;
}

.cloud {
  position: relative;
  display: flex;
  flex-direction: column;
/*  align-items: center;*/
  width: fit-content;
}

.more-clouds {
  animation: floating_big 20s ease-in-out infinite;
  max-width: 100%;
  box-sizing: content-box;
  position: relative;
  z-index: 3;
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-self: flex-start;
  opacity: 0.6;
  transform: scale(2);
}

.more-clouds:nth-of-type(2) {
  animation: floating_big 20s ease-in-out 10s both infinite;
  top: -20vh;
}

.more-clouds :nth-child(2) {
  left: 4vw;
  top: 1vw
}

.more-clouds :nth-child(3) {
  left: 0;
  top: -2vw
}

.heavy-clouds {
  position: absolute;
  opacity: 0.2;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.cloud__top {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.cloud__l {
  background-image: radial-gradient(#fff, #edf, #cbe);
  width: 5vmax;
  height: 3vmax;
  border-radius: 50% 40% 0% 30%;
}

.cloud__c {
  background-image: radial-gradient(#fff, #edf, #cbe);
  width: 10vmax;
  height: 5vmax;
  border-radius: 50% 40% 0% 0%;
}

.cloud__r {
  background-image: radial-gradient(#fff, #edf, #cbe);
  width: 4vmax;
  height: 4vmax;
  border-radius: 50% 40% 50% 0%;
}

.cloud__bot {
  position: relative;
  top: -1vmax;
  left: 2vmax;
  width: 15vmax;
  height: 2vmax;
  background-image: radial-gradient(#cbe, #9bc, #8af);
  border-radius: 80%;
}

.more-clouds .cloud__bot {
  top: -1vmax;
  left: 2.5vmax;
}
