.main {
  margin: 0;
  padding: 0;
  width: 100%;
  max-height: 100vh;
}

.sky {
  width: 100%;
  height: calc(100vh - 20vw);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.sky_0 {
  background-image: linear-gradient(#102, #426);
}

.sky_1 {
  background-image: linear-gradient(#236, #aa5);
}

.sky_2 {
  background-image: linear-gradient(#6ac, #45b);
}

.sky_3{
  background-image: linear-gradient(#45b, #829);
}

.ground {
  width: 100%;
  height: 20vw;
  background-image: linear-gradient(#2c2, #48b);
  position: relative;
  z-index: 6;
}

@media (min-aspect-ratio: 2) {
  .sky {
    height: calc(100vh - 40vh);
  }
  .ground {
    height: 40vh;
  }
}

@media (max-aspect-ratio: 1/1) {
  .sky {
    height: calc(80vh);
  }

  .ground {
    height: 20vh;
  }

  .hill:nth-of-type(3) {
    display: none;
  }
}

@media (max-aspect-ratio: 7/10) {
  .hill:nth-of-type(2) {
    display: none;
  }
}

@media (max-aspect-ratio: 1/2) {
  .hill {
    bottom: 0;
    left: calc(50vw - 15vmax);
  }
}

@keyframes floating {
  from {
    margin-left: -60vw;
  }

  to {
    margin-left: 160vw;
  }
}

@keyframes floating_big {
  from {
    margin-left: -110vw;
  }

  to {
    margin-left: 160vw;
  }
}

@keyframes floating_big2 {
  from {
    margin-left: -190vw;
  }

  to {
    margin-left: 80vw;
  }
}

@keyframes spin {
  from {
    rotate: 0;
  }

  to {
    rotate: 360deg;
  }
}