.button {
  width: fit-content;
  height: 36px;
  min-width: 36px;
  box-sizing: border-box;
  border: #224 double 3px;
  border-radius: 5px;
  background-color: inherit;
  color: inherit;
  margin: 0;
  padding: 5px;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
}

.button:hover {
  background-color: #222;
  color: #999;
}

.button:disabled {
  opacity: 0.2;
}
