.thunder {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 2;
}

.flash {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: #ffd;
  z-index: 9;
  animation: strike 8s infinite 0.1s both;
}

.flash:nth-of-type(2) {
  animation: strike 7s infinite 2.1s both;
}

.flash:nth-of-type(3) {
  animation: strike 7.5s infinite 4.6s both;
}

.flash:nth-of-type(4) {
  animation: strike 9.5s infinite 6.1s both;
}

.chain {
  animation: strike 8s infinite both;
}

.chain:nth-of-type(2) {
  animation: strike 7s infinite 2s both;
}

.chain:nth-of-type(3) {
  animation: strike 7.5s infinite 4.5s both;
}

.chain:nth-of-type(4) {
  animation: strike 9.5s infinite 6s both;
}


.light {
  position: absolute;
  width: 0.5vw;
  height: 0.7vh;
  left: 50vw;
  right: 50vh;
  border-radius: 30%;
  background-color: #ffc;
}

@keyframes strike {
  0% {
    opacity: 0;
  }

  2% {
    opacity: 0.9;
  }

  4% {
     opacity: 0;
   }

   100% {
     opacity: 0;
   }
}