.mill {
  position: relative;
  bottom: -5vmax;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 8;
}

.mill__top {
  position: relative;
  top: 3vmax;
  left: 45%;
  width: 1vmax;
  height: 1vmax;
  border-radius: 50%;
  background-color: #224;
  animation: spin normal 10s ease-in infinite;
}

.mill__wing {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-items: flex-start;
}

.mill__wing:nth-of-type(1) {
  bottom: 1vmax;
  left: 0;
}

.mill__wing:nth-of-type(2) {
  transform: rotate(180deg);
  top: 1vmax;
  left: -4vmax;
}

.mill__wing:nth-of-type(3) {
  transform: rotate(270deg);
  bottom: -3.5vmax;
  left: -8.5vmax;
}

.mill__wing:nth-of-type(4) {
  transform: rotate(90deg);
  bottom: -7.5vmax;
  left: 4.5vmax;
}

.mill__line {
  width: 1vmax;
  height: 12vmax;
  background-color: #002;
}

.mill__sail {
  border-right: 3vmax solid transparent;
  border-left: 1vmax solid #887;
  border-top: 10vmax solid #887;
  border-radius: 0 0 6vmax 0;
}

.mill__mid {
  border-left: 8vmax solid transparent;
  border-right: 8vmax solid transparent;
  border-bottom: 10vmax solid #c66;
}

.mill__bot {
  display: flex;
  flex-direction: row;
}

.mill__l {
  border-left: 5vmax solid transparent;
  border-right: 1vmax solid #cc6;
  border-bottom: 20vmax solid #cc6;
  border-radius: 0 0 0 4vmax;
}

.mill__c {
  width: 6vmax;
  height: 20vmax;
  background-color: #aa2;
}

.mill__r {
  border-right: 5vmax solid transparent;
  border-left: 1vmax solid #880;
  border-bottom: 20vmax solid #880;
  border-radius: 0 0 4vmax 0;
}